.card-wrapper {
    padding: 1rem 1.85rem;
    margin: .22rem; 
    display: inline-grid;
    grid-template-columns: 2fr 2fr;
    grid-template-rows: 2fr 2fr;
    grid-gap: 0.3rem;
    text-align: center;
    align-items: center;
    transition-duration: 300ms;
    transition-timing-function: ease-in;
    transition-property: all;
    
}

.card-wrapper:hover#web {
    background: #dcf2c6;
}

.card-wrapper:hover#crypto {
    background: #f6f2c6;
}

.card-wrapper:hover#marketing {
    background: #e9ccff;
}

.card-wrapper:hover#gui {
    background: #adffaf;
}

.card-wrapper:hover#robots {
    background: #cfb6f2;
}

.card-wrapper:hover#analog {
